.discounts {
    background-color: #F2F2F2;
    position: relative;

    &__container {
        padding-bottom: 100px;
        padding-top: 63px;
        padding-right: 0;
        // padding-bottom: 100px;
    }

    &__swiper-wrapper {
        width: 270px !important;
        display: flex;
    }

    &__swiper-slide {
        margin-right: 10px;
    }

    &__card {
        // display: none;
        width: 269px;
        padding-top: 26px;
        padding-bottom: 26px;
        color: #FFFFFF;
        position: relative;
        padding-left: 26px;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.55) 92.71%);
            z-index: 0;
        }

        &-title {
            z-index: 10;
            font-size: 30px;
            max-width: 160px;
            margin-bottom: 16px;
        }

        &-description {
            z-index: 10;
            color: #E3E3E3;
            max-width: 225px;
            margin-bottom: 26px;
        }

        &-bg {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;

        }

        &-button {
            z-index: 10;
            width: auto;
            padding: 16px 23px;
            line-height: 120.3%;
            /* or 17px */
            letter-spacing: 0.105em;
        }
    }

